export default class SmartGenerator {
  constructor() {
    this.firstHahs = new Set();
    this.secondHahs = new Set();
    this.firstLimit = null;
    this.secondLimit = null;
  }

  randInt(min, max) {
    min += 1;
    max -= 1;
    return Math.floor(Math.random() * (max - min + 1)) + min;
  }

  getFisrt(max, min) {
    let num = this.randInt(max, min);

    while (this.firstHahs.has(num)) {
      num = this.randInt(max, min);
    }

    this.firstHahs.add(num);
    return num;
  }

  getSecond(max, min) {
    let num = this.randInt(max, min);

    while (this.secondHahs.has(num)) {
      num = this.randInt(max, min);
    }

    this.secondHahs.add(num);
    return num;
  }

  generateNumbers(first, second) {
    const firstDiff = first.max - first.min;
    const secondDiff = second.max - second.min;

    if (this.firstLimit === null) {
      this.firstLimit = first;
    } else {
      if (JSON.stringify(first) !== JSON.stringify(this.firstLimit)) {
        this.firstHahs.clear();
      }
    }

    if (this.secondLimit === null) {
      this.secondLimit = second;
    } else {
      if (JSON.stringify(second) !== JSON.stringify(this.secondLimit)) {
        this.secondHahs.clear();
      }
    }

    if (firstDiff <= this.firstHahs.size) {
      this.firstHahs.clear();
    }
    if (secondDiff <= this.secondHahs.size) {
      this.secondHahs.clear();
    }

    const firstNumber = this.getFisrt(first.max, first.min);
    const secondNumber = this.getSecond(second.max, second.min);

    return {
      firstNumber,
      secondNumber
    };
  }
}
